const dom = require("./controller/dom");
const domStrings = new dom.DomStrings();

import "../scss/style.scss";
import { WeatherData } from "./controller/searchController";
import { Card } from "./controller/dailyController";
import { updateHero, updateData, extraInfo } from "./view/heroView";
import { async } from "regenerator-runtime";
import { renderCard, sunMoon, dayDetails, pieChart } from "./view/dailyView";
import font from "../font/*.otf";

// const get data = async () => {
//
// };
const removePreloader = () => {
  domStrings.preloader.classList.add("remove_preloader");
  // start animation
  AOS.init();
};

const state = {};
const controlSearch = async () => {
  // ##################### weather from ip address ##########################
  const ip = new WeatherData();
  await ip.ipweather();
  state.result = await ip.getIpData();
  // update hero object
  updateHero(
    state.result.name,
    state.result.sys.country,
    state.result.weather[0].icon,
    state.result.main.temp,
    state.result.weather[0].description,
    state.result.weather[0].main,
    ip.country
  );
  // extra info
  extraInfo(ip.feel, ip.wind, ip.vis, ip.temp, ip.humi, ip.pressure);
  // card
  const card = new Card(ip.lat, ip.lon);
  const cardDataIp = await card.getData();
  // console.log(cardDataIp);
  // console.log(cardDataIp.length);
  domStrings.dailyCard.innerHTML = "";

  for (let i = 0; i < Math.min(6, cardDataIp.length); i++) {
    renderCard(
      cardDataIp[i].dt,
      cardDataIp[i].temp.max,
      cardDataIp[i].temp.min,
      cardDataIp[i].weather[0].icon,
      cardDataIp[i].weather[0].description.split(" ").join("<br>")
    );
  }

  dayDetails(
    ip.maxTemp,
    ip.minTemp,
    cardDataIp[0].weather[0].description,
    state.result.weather[0].description
  );
  pieChart(ip.ppt, cardDataIp[0].uvi, ip.humi, ip.wind);

  sunMoon(ip.sunrise, ip.sunset, ip.timezone);
  // dayData(ip.lat, ip.lon);
  // %%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
  // ##################search for city########################
  // %%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
  // after search
  domStrings.searchFormBtn.addEventListener("click", async (e) => {
    e.preventDefault();
    delete state.result;

    let query = updateData(e);

    if (query) {
      const search = new WeatherData(query);
      state.result = await search.getData();
      // console.log(search.f);
      updateHero(
        state.result.name,
        state.result.sys.country,
        state.result.weather[0].icon,
        state.result.main.temp,
        state.result.weather[0].description,
        state.result.weather[0].main,
        search.country
      );
      extraInfo(
        search.feel,
        search.wind,
        search.vis,
        search.temp,
        search.humi,
        search.pressure
      );
      const card = new Card(search.lat, search.lon);
      const cardData = await card.getData();
      dayDetails(
        search.maxTemp,
        search.minTemp,
        cardData[0].weather[0].description,
        state.result.weather[0].description
      );

      // render card
      domStrings.dailyCard.innerHTML = "";

      for (let i = 0; i < Math.min(6, cardData.length); i++) {
        renderCard(
          cardData[i].dt,
          cardData[i].temp.max,
          cardData[i].temp.min,
          cardData[i].weather[0].icon,
          cardData[i].weather[0].description.split(" ").join("<br>")
        );
      }
      // day details

      pieChart(search.ppt, cardData[0].uvi, search.humi, search.wind);
      domStrings.input.value = "";
      sunMoon(search.sunrise, search.sunset, search.timezone);
    } else {
      alert("Please Enter City Name");
    }
  });
  removePreloader();
};
controlSearch();
