export const dayData = async (lat, lon) => {
  try {
    const data = await axios(
      `https://api.ipgeolocation.io/astronomy?apiKey=${dayDetailsApiKey}&lat=${lat}long=${lon}`
    );
    console.log(data.data);
  } catch (e) {
    console.log(e);
  }
};

// pie chart gen

const chart = (percentage, color, value, piechart, type) => {
  let html = `<svg class="pie" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 81 81">
  <defs>
    <style>
      .a${type} {
        fill: none;
        stroke: ${color};
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-width: 4px;
        stroke-dasharray: ${percentage === 0 ? 1 : percentage * 2} 200;
        stroke-dashoffset: 0;
        transform: rotate(270deg);
        transform-origin: 50% 50%;
      }
      .c${type} {
        fill: none;
        stroke: rgba(255, 255, 255, 0.204);
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-width: 4px;

        stroke-dashoffset: 0;
        transform: rotate(270deg);
        transform-origin: 50% 50%;
      }
      .b${type} {
        font-size: 36px;
        font-family: Phenomena-Thin, Phenomena;
        fill: white;
        font-weight: 100;
      }
    </style>
  </defs>
  <circle class="a${type}" cx="50%" cy="50%" r="40%" />
  <circle class="c${type}" cx="50%" cy="50%" r="40%" />
  <text
    class="b${type}"
    x="50%"
    y="50%"
    dominant-baseline="middle"
    text-anchor="middle"
  >
   ${Math.floor(value)}
  </text>
</svg>`;
  // console.log(html);
  piechart.innerHTML = html;
};

const domStrings = new dom.DomStrings();
const dayDate = (timestamp) => {
  const dates = new Date(timestamp * 1000);
  const dt = dates
    .toGMTString()
    .split(" ")
    .slice(0, 2)
    .join(" ")
    .replace(",", "");
  return dt;
};
export const renderCard = (timestamp, maxTemp, minTemp, logo, des) => {
  const dt = dayDate(timestamp);
  const html = ` <div
  class="daily__card"
  data-aos="fade-up"
  data-aos-duration="1000"
  data-aos-delay="100"
><h2 class="daily__card--date">${dt}</h2>
          <img src="${svg[logo]}" alt="" class="daily__card--sign" />
          <h3 class="daily__card--temp">
            ${Math.floor(
              maxTemp - 273.15
            )}<sup>o</sup> <grey>|</grey> <span>${Math.floor(
    minTemp - 273.15
  )}<sup>o</sup></span>
          </h3>
          <div class="daily__card--status">${des}</div></div>`;

  domStrings.dailyCard.insertAdjacentHTML("beforeend", html);
};
export const dayDetails = (maxTemp, minTemp, desNight, desDay) => {
  let html = `<h2 class="detail__daily--header">Day</h2>
         <p>
           Expect ${desDay}. <br />
           The high will be %MAX%
         </p>
         <h2 class="detail__daily--header">Night</h2>
         <p>
           Expect ${desNight}. <br />
           The high will be %MIN%
         </p>`;
  html = html.replace("%MAX%", `${Math.floor(maxTemp - 273.13)}`);
  html = html.replace("%MIN%", `${Math.floor(minTemp - 273.13)}`);
  domStrings.prediction.innerHTML = html;
};

export const pieChart = (ppt, uv, hum, win) => {
  uvIndex(uv);
  precitipation(ppt);
  humidity(hum);
  wind(win);
};

const uvIndex = (uv) => {
  let color;
  const percentage = (uv * 100) / 16;
  if (uv >= 11) {
    color = "#ff304f"; //red
  } else if (uv >= 8 && uv < 11) {
    color = "coral"; //orange
  } else if (uv >= 6 && uv < 8) {
    color = "#edcf54"; //yellow
  } else if (uv >= 0 && uv < 6) {
    color = "#2fba7e"; //green
  }

  // render uv chart
  chart(percentage, color, uv, domStrings.pieUv, "_uv");
};
const precitipation = (ppt) => {
  let color;
  const percentage = ppt;
  if (ppt >= 75) {
    color = "#ff304f"; //red
  } else if (ppt >= 50 && ppt < 75) {
    color = "coral"; //orange
  } else if (ppt >= 25 && ppt < 50) {
    color = "#edcf54"; //yellow
  } else if (ppt >= 0 && ppt < 25) {
    color = "#2fba7e"; //green
  }
  // console.log(percentage, color, ppt);
  chart(percentage, color, ppt, domStrings.piePpt, "_ppt");
};
const humidity = (hum) => {
  const percentage = hum;
  let color;
  if (hum >= 75) {
    color = "#ff304f"; //red
  } else if (hum >= 50 && hum < 75) {
    color = "coral"; //orange
  } else if (hum >= 25 && hum < 50) {
    color = "#edcf54"; //yellow
  } else if (hum >= 0 && hum < 25) {
    color = "#2fba7e"; //green
  }

  chart(percentage, color, hum, domStrings.pieHum, "_hum");
};
const wind = (wind) => {
  const percentage = wind;
  let color;
  if (wind >= 75) {
    color = "#ff304f"; //red
  } else if (wind >= 50 && wind < 75) {
    color = "coral"; //orange
  } else if (wind >= 25 && wind < 50) {
    color = "#edcf54"; //yellow
  } else if (wind >= 0 && wind < 25) {
    color = "#2fba7e"; //green
  }

  chart(percentage, color, wind, domStrings.pieWind, "_wind");
};
const time = (timestamp, timezone) => {
  if (timestamp) {
    const date = new Date((timestamp + timezone) * 1000);
    const time = date.toGMTString().split(" ")[4].split(":").slice(0, -1);
    return time;
  } else {
    return ["??", "??"];
  }
};
export const sunMoon = (
  sunrise,
  sunset,
  timezone = 0,
  moonrise = 0,
  moonset = 0
) => {
  const sunHtml = `<h2 class="detail__daily--header">Sunrise</h2>

<p>
  <img
    src="${svg.sunrise}"
    class="detail__sun--logo"
  /><img /> ${time(sunrise, timezone)[0]}<span>: ${
    time(sunrise, timezone)[1]
  }</span>
</p>

<h2 class="detail__daily--header">Sunset</h2>

<p>
  <img
    src="${svg.sunset}"
    class="detail__sun--logo"
  /><img />  ${time(sunset, timezone)[0]}<span>: ${
    time(sunset, timezone)[1]
  }</span>
</p>`;
  domStrings.sun.innerHTML = sunHtml;
  const moonHtml = `<h2 class="detail__daily--header">Moonrise</h2>
  <p>
  <img
  src="${svg.moon}"
  class="detail__moon--logo"
  /><img /> ${time(moonrise, timezone)[0]}<span>:${
    time(moonrise, timezone)[1]
  }</span>
  </p>
  <h2 class="detail__daily--header">Moonset</h2>
  <img src="${svg.moon}" class="detail__moon--logo" />
  <p>${time(moonset, timezone)[0]}<span>:${
    time(moonset, timezone)[1]
  }</span></p>`;
  domStrings.moon.innerHTML = moonHtml;
};
