import { async } from "regenerator-runtime";

const axios = require("axios");
require("regenerator-runtime");

export const weatherApiKey = "99ec011442a6f9d0db115caf4aa87f64";
export const dayDetailsApiKey = "6dbecf49040f451a930d0dbc5f0c0717";
const ipApiKey = "1be076e189bbfd5ea1e1c778721a0c9c";

export class WeatherData {
  constructor(query) {
    this.query = query;
  }
  async ipweather() {
    try {
      const data = await axios(
        `http://api.ipstack.com/check?access_key=${ipApiKey}&format=1
        `
      );
      // if id is null defalt id is 1275339 fro mumbai
      this.id = data.data.location.geoname_id || 1275339;
    } catch (e) {
      console.log(e);
    }
  }

  async getData() {
    try {
      // console.log(this.query);
      const data = await axios(
        `https://api.openweathermap.org/data/2.5/weather?q=${this.query}&appid=${weatherApiKey}`
      );
      // console.log(data.data);
      this.feel = data.data.main.feels_like;
      this.wind = data.data.wind.speed;
      this.vis = data.data.visibility;
      this.temp = data.data;
      this.pressure = data.data;
      this.humi = data.data.main.humidity;
      this.lat = data.data;
      this.lat = data.data.coord.lat;
      this.lon = data.data.coord.lon;
      this.ppt = data.data.clouds.all;
      this.sunrise = data.data.sys.sunrise;
      this.sunset = data.data.sys.sunset;
      this.timezone = data.data.timezone;
      this.country = data.data.sys.country;
      this.maxTemp = data.data.main.temp_max;
      this.minTemp = data.data.main.temp_min;
      // this.sunrise = data.data.console.log(data.data);
      // console.log(this.country);
      return data.data;
    } catch (e) {
      alert(e);
    }
  }
  async getIpData() {
    try {
      // console.log(this.query);
      const data = await axios(
        `https://api.openweathermap.org/data/2.5/weather?id=${this.id}&appid=${weatherApiKey}`
      );
      // console.log(data);
      this.feel = data.data.main.feels_like;
      this.wind = data.data.wind.speed;
      this.vis = data.data.visibility;
      this.temp = data.data.main.temp;
      this.pressure = data.data.main.pressure;
      this.humi = data.data.main.humidity;
      this.lat = data.data.coord.lat;
      this.lon = data.data.coord.lon;
      this.maxTemp = data.data.main.temp_max;
      this.minTemp = data.data.main.temp_min;
      this.ppt = data.data.clouds.all;
      this.sunrise = data.data.sys.sunrise;
      this.sunset = data.data.sys.sunset;
      this.timezone = data.data.timezone;
      this.country = data.data.sys.country;
      return data.data;
    } catch (e) {
      alert(e);
    }
  }
}
