import svg from "../../img/SVG/*.svg";
import png from "../../img/bg/*.png";
const dom = require("../controller/dom");
const domStrings = new dom.DomStrings();

const dewPointCalc = (temp, humi) => {
  const tempC = temp - 273.15;
  let ans =
    tempC -
    (14.55 + 0.114 * tempC) * (1 - 0.01 * humi) -
    Math.pow((2.5 + 0.007 * tempC) * (1 - 0.01 * humi), 3) -
    (15.9 + 0.117 * tempC) * Math.pow(1 - 0.01 * humi, 14);
  return Math.floor(ans);
};

export const extraInfo = (feel, wind, vis, temp, humi, pressure) => {
  let html = domStrings.heroExtra1.innerHTML;
  html = html.replace("%FL%", `${Math.floor(feel - 273.15)}`);
  html = html.replace("%W%", wind);
  html = html.replace("%V%", `${vis / 1000}`);
  domStrings.heroExtra1.innerHTML = html;
  // console.log(html);

  // dew point calc

  const dewPoint = dewPointCalc(temp, humi);

  html = domStrings.heroExtra2.innerHTML;
  html = html.replace("%P%", pressure);
  html = html.replace("%H%", humi);
  html = html.replace("%D%", dewPoint);
  // console.log(html);
  domStrings.heroExtra2.innerHTML = html;
};
export const updateHero = (
  city,
  country,
  logo,
  temp,
  status,
  img,
  flag = "IN"
) => {
  // console.log(logo);

  domStrings.city.textContent = ``;
  domStrings.city.textContent = `${city}, ${country}`;
  // console.log("flag", flag);
  domStrings.flag.src = `https://www.countryflags.io/${flag}/flat/64.png`;
  // console.log(`./img/SVG/${logo}.svg`);
  domStrings.sign.src = ``;
  // console.log(logo);
  domStrings.sign.src = `${svg[logo]}`;
  domStrings.temp.innerHTML = "<span>o</span>C";
  domStrings.temp.insertAdjacentText(
    "afterbegin",
    `${Math.floor(temp - 273.15)}`
    // extra info
  );
  // extraInfo(300, 15, 8, 298, 65, 1000);
  domStrings.status.textContent = `${status}`;
  // update bg img
  bgupdate(img, logo);
};
export const updateData = (e) => {
  e.preventDefault();
  if (domStrings.input.value) {
    domStrings.search.classList.remove("search__active");
    domStrings.bgImg.classList.remove("main__bg-scale");
    domStrings.main.classList.remove("main__bg-blur");
    domStrings.body.classList.remove("overflow");
    return domStrings.input.value;
    // console.log();
  }
};
const bgupdate = (Img, icon) => {
  let img = Img.toLowerCase();
  if (icon === "01n") {
    // console.log(png.night);
    domStrings.bgImg.src = ``;
    domStrings.bgImg.src = `${png.night}`;
  } else if (png[img]) {
    // console.log(img);
    domStrings.bgImg.src = ``;
    domStrings.bgImg.src = `${png[img]}`;
  } else {
    domStrings.bgImg.src = ``;
    domStrings.bgImg.src = `${png.haze}`;
  }
};
