// import { Domstrings } from "./dom";
const axios = require("axios");
import { weatherApiKey } from "./searchController";
// const domstrings = new Domstrings();

export class Card {
  constructor(lat, lon) {
    this.lat = lat;
    this.lon = lon;
  }
  async getData() {
    // console.log(this.lat, this.lon);
    try {
      const data = await axios(
        `https://api.openweathermap.org/data/2.5/onecall?lat=${this.lat}&lon=${this.lon}&exclude=current,minutely,hourly&appid=${weatherApiKey}`
      );
      //       this.date = data.data.daily.dt;
      //       this.maxTemp = data.data.daily.temp.max;
      //       this.minTemp = data.data.daily.temp.min;
      //       this.des = data.data.daily.weather[0].description;
      //       this.des = data.data.daily.weather[0].icon;
      return data.data.daily;
      //       console.log(data.data.daily);
    } catch (e) {
      console.log(e);
    }
  }
}
